/* Form.css */

.form-container {
    max-width: 700px;
    margin: 0 auto;
    padding: 25px;
    border: 1px solid #333030;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-bottom: 0.5rem;
  }
  
  .form-content {
    text-align: center;
  }
  
  .input-field {
    width: 100%;
    margin-bottom: 15px;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .input-field[type="email"],
  .input-field[type="tel"],
  .input-field[type="text"] 
  {
    max-width: 80%;
    margin-top: 15px;
  }

  .form-content textarea {
    width: 77%;
    padding: 20px
  }
  
  .submit h2 {
    font-size: 30px;
    margin-bottom: -3rem;
    margin-top: -10rem;
  }
  
  .form-content select {
    margin: 10px;
  }

  .submit-icon {
    width: 60%;
    margin-bottom: -25px;
  }
  
  .submit button {
    padding: 0.8rem;
  }
  
  .back-button:hover {
    background-color: #090d0e;
  }
  
  
  /* Responsive Styles */
  @media screen and (max-width: 600px) {
    .form-container {
      padding-top: 20%;
      max-width: 80%;}
  
    .form-content textarea {
      width: 58vw;
    }  
    
    .form-content {
      text-align: center;
      padding-top: 5vh;
    }

    .submit-icon {
      width: 140%;
      margin-top: -2rem;
    }
    .submit {
      width: 100%;
      max-width: 80%;
      margin: 0 auto;
      margin: 0.5rem;
    }

    .submit h2 {
      margin: 0 auto;
      margin-top: -120%
    }

    .submit button {
      background-color: #45a049;
      margin-bottom: -18rem;
      margin-top: 20px;
    }
  
    .submit button:hover {
      background-color: #090d0e;
    }
  
    .submit-container {
      max-width: 100%;
      display: flex;
      justify-content: center; /* Center horizontally */
      align-items: center; /* Center vertically */
      height: 80vh;
    }
  }