/* Internship.css */

.internship-container {
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
  padding: 20px;
  box-sizing: border-box;
}

.header h1 {
  margin: 0;
  font-size: 1.8rem;
  background-color: black;
  color: hsl(0, 0%, 100%);
  padding: 2rem;
  font-family: 'Poppins', sans-serif;
  text-align: center;
}

.internship-scroll-container {
  display: flex;
  gap: 20px;
  box-sizing: border-box;
  height: 22rem;
}
/* Internship.css */

.internship-card {
  width: 275px; /* Fixed width */
  height: 350px; /* Fixed height */
  display: flex;
  flex-direction: column;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  background-color: #fff;
  padding: 20px;
  box-sizing: border-box;
  font-size: 10%;
  position: relative;
  padding-top: 40px;
  text-decoration: none;
}

.internship-card:hover {
  transform: scale(1.1);
}

.internship-card img {
  width: 100%; /* Set image width to 100% of its container */
  height: 150px; /* Set fixed height for the images */
  object-fit: cover; /* Maintain aspect ratio and cover the container */
  margin-bottom: 10px; /* Add bottom margin to create space between image and text */
}

/* Rest of your styles... */


.internship-card p {
  margin: 10px;
  font-size: 0.75rem;
  text-align: justify;
  text-align-last: center;
}

.internship-card h2 {
  color: rgb(24, 24, 24);
  text-decoration: none;
  font-size: 1rem;
  margin-bottom: 15px;
  text-align: center;
  margin-top: -20px;
  padding-top: 20;
  font-weight: bold; /* Added bold font for h2 */
}




/* Media query for mobile devices */
@media screen and (max-width: 768px) {
  

  .header h1 {
    padding-top: 100px;
  }
  
}