/* InternshipSection.css */

.internship-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(253, 236, 84);
  height: 100vh;
  text-align: center;
  padding: 1vh;
}

.find-domains {
  width: 80%
}

.content  {
  width: 50%;
}

.content h2 {
  font-size: 6vh;
}

.textRunning {
  animation: textRunning 3s linear infinite;
  font-size: 2.2vh; 
  max-width: 100%;
  margin-bottom: 50px;
}

.intern-image {
  width: 100%;
  opacity: 0;
  border-radius: 15px;
  animation: fadeIn 1s ease-in-out forwards, slideUp 1s ease-in-out forwards;
}

/* Define animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideUp {
  from {
    transform: translateY(20px);
  }
  to {
    transform: translateY(0);
  }
}

@keyframes textRunning {
  0% {
    transform: scale(1);
    opacity: 0.4;
  }
  50% {
    transform: scale(1.1);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0.4;
  }
}

/* Media query for responsiveness */
@media screen and (max-width: 768px) {
  .internship-section{
    max-width: 100%;
    height: auto;
  }
  .intern-image {
    width: 100%;}

  .content h2 
  {
    font-size: 130%;
    text-align: center;
    width: 100%
  }

  .content p {
    text-align: justify;
    font-size: 80%;
  }
  .textRunning {
    animation: textRunning 0s linear infinite;
    text-align: justify;
  }
}